.singlePost
{
    padding: 50px 0px;
    background: #fff;
}
.singlePost .right
{
    margin-top: 30px;
}
.singlePost img
{
    width: 100%;
    height: 800px;
    border-radius: 10px;
    object-fit:cover;
}
.singlePost h1
{
    font-size:30px;
    font-weight: 500;
}
.singlePost p
{
    margin: 20px 0;
    font-size: 18px;
    line-height: 2;
    text-transform: capitalize;
}
.singlePost .buttons
{
    display: flex;
    justify-content: flex-end;
}
.singlePost .button
{
    margin-left: 20px;
    border: none;
}
