.newPost .container form textarea
{
    resize: none;
}
.newPost
{
    margin: 60px 0;
}
.newPost .container
{
    max-width:50%;
}
.newPost i
{
    font-size:25px;
    margin-right: 20px;
    margin-top: -20px;
}
.newPost img
{
    width: 100%;
    height: 300px;
    object-fit: cover;
}
.newPost .img
{
    position: relative;
}
.newPost form
{
    width: 100%;
    margin: 0;
    padding: 0;
    box-shadow: none;
    border:none;
}
.newPost .inputFile input
{
    padding-top: 9px;
    margin-top: 20px;
    width: 100%;
    height: 40px;
    position: relative;
    z-index:2;
    opacity:1;
}
.newPost .img img
{
    /* position: absolute;
    top:0;
    left:0; */
    width:100%;
    height: 300px;
    z-index: 1;
    border-radius: 5px;
}